<template lang="pug">
  input.form-input(
    :value="value"
    :type="type"
    :style="style"
    @input="inputed"
    @blur="blured"
  )
</template>

<script>
import patterns from '@/utilities/patterns'

export default {
  name: 'form-input',

  props: {
    value: {type: String, default: ''},
    type: {type: String, default: 'text'},
    pattern: {type: String},
    addonsCount: {type: Number, default: 0},
  },

  computed: {
    style() {
      const addonWidth = 28
      return this.addonsCount ? `padding-right: ${addonWidth * this.addonsCount}px` : ''
    },
  },

  methods: {
    inputed(e) {
      let value =  e.target.value
      if (this.pattern) console.log(patterns[this.pattern](value))
      // if (this.pattern) value = patterns[this.pattern](value)
      this.$emit('input', value)
    },
    blured() {
      this.$emit('blur')
    },
  },
}
</script>
